// /// front banner section

/////TO DO change banner img to glass tumbler coffee cup for mobile

.events-banner {
  background: url('../../assets/events-images/event-hero@3x.png') no-repeat;
  height: 100vh;
  background: contain;
  background-position: bottom center;

  @include desktop {
    background-image: url('../../assets/events-images/coffee-cup.jpg');
    background: contain center;
  }
}

.upcoming-events {
  position: center;
  background-color: #6f8f6a;
  padding: 40px;
  opacity: 0.85;
  width: 80%;
  height: 50%;
  top: 25%;
}

h1.upcoming-events-title {
  position: relative;
  color: #fff;
  font-family: $headfont;
  font-style: bold;
  font-size: 1.5em;

  // padding-top: inherit;
}

h2.upcoming-events-location {
  position: relative;
  color: #fff;
  font-size: 1em;
}

.banner-links {
  display: flex;
  align-content: center;
  padding: 20px;
  flex-direction: column;
  justify-content: space-around;
  min-width: 1vw;
}

.buy-tickets-btn {
  background: #fff;
  color: $primary_green_med;
  border: 3px solid $primary_green_med;
  border-radius: 1.5em;
  // padding: 15px;
  font-size: $fontsize;
  font-family: $parafont;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  margin: 2% 28%;
  padding: 12px;
  width: 200px;
  white-space: no-wrap;
}

