.apply-to-pitch-btn {
  background: #fff;
  text-decoration: none;
  color: $primary_green_med;
  border: 3px solid $primary_green_med;
  border-radius: 1.5em;
  // padding: 15px;
  font-size: $fontsize;
  font-family: $parafont;
  font-weight: 500;
  text-transform: uppercase;
  margin: 2% 28%;
  padding: 12px;
  max-width: 34vw;
  position: relative;
  width: 200px;
  white-space: no-wrap;
}

