// ///pitch summary section //

.pitch-summary {
  display: flex;
  margin-left: 50px;
  flex-direction: column;
  align-items: center;
  width: 100%vw;
  text-align: center;

  @include desktop {
    flex-direction: row;
  }
}

.summary-image {
  img {
    max-width: 100%;
    height: auto;
  }
}

.flex-item-container {
  display: flex;
  // justify-content: center;
  // align-items: center;
  margin: 0 60px;
  flex-direction: column;
  height: 60vh;
}

h3.summary-title {
  font-size: 1.5em;
  font-family: $headfont, serif;
  text-align: center;
  font-display: bold;
  color: black;
}

p.summary-description {
  font-size: $fontsize;
  text-align: center;
  max-width: 500px;
}

.apply-to-pitch-btn-green-flex {
  display: flex;
  justify-content: center;
}

.apply-to-pitch-btn-green {
  background: $primary_green_med;
  color: white;
  border: 1px solid transparent;
  border-radius: 2em;
  font-size: $fontsize;
  font-family: $parafont;
  text-align: center;
  text-transform: uppercase;
  width: 217px;
  height: 45px;
  text-decoration: none;
  font-weight: 500;
  overflow: hidden;

  // &:visited {
  //   // cursor: pointer;
  //   // // border: 1px solid $primary_green_dark;
  //   // color: white;
  //   // background: white;
  // }
}
