/////past events

h2.past-events-title {
  text-align: center;
  font-size: 2em;
  position: relative;

  @include desktop {
    text-align: left;
    padding-left: 221px;
    padding-bottom: 10px;
  }
}

.past-events-container {
  display: flex;
  // flex-wrap: wrap;
  flex-direction: column;
  padding-top: 20px;
  margin: 0 auto;
  // border: 1px solid black;

  .past-event {
    border: 1px solid black;
    display: flex;
    max-width: 600px;
    flex-direction: column;
  }

  @include desktop {
    background: #f2f7f7;
    background-image: url(../../assets/events-images/event-journal.jpg);
    width: 84%;
    background-size: 100vh;
    background-repeat: no-repeat;
    // background-position: top right;
    flex-direction: column;
    max-width: 1000px;
    background-position: 143% -16%;
  }

  .past-event {
    @include desktop {
      flex-direction: row;
    }
  }
}

.past-events-text {
  // flex: 1;
  width: 60%;
  @include desktop {
    // width: 100vh;
    flex-direction: column;
  }
}
.past-events-container {
  img {
    object-fit: cover;
    // height: 30vh;
    height: 200px;
    width: 200px;

    // justify-content: center;
    position: relative;
    padding-left: 0;
    margin: 0 auto;

    @include desktop {
      padding: 20px;
    }
  }
}

.past-events-date {
  font-size: 1.75em;
  text-align: center;
  line-height: 1em;
  padding-top: 0;
}
.past-events-location {
  color: $primary_green_med;
  font-size: 1.5em;
  text-align: center;
}
.past-events-description {
  font-size: $fontsize;
  margin: 5% 5%;
  line-height: $fontsize;
  text-align: center;
}
